    import appRichText from "../../../components/basic-component/app-rich/parse.vue";

    import { mapState } from "vuex";

    export default {
        data() {
            return {
		        _num: 0,
		        explainLeft: '0rpx',
		        move: false,
		        prop: 0,
		        name: '',
		        money: '',
		        triangleLeft: 8,
		        member: true,
		        show: true,
		        index: 0,
		        list: [],
		        detail: [],
            }
        },
        name: "detail",
        components: {
            "app-rich-text": appRichText,
        },
        computed: {
            ...mapState({
                memberImg: state => state.mallConfig.__wxapp_img.member,
            })
        },
        methods: {
		    toMove() {
		        this.move = true
		    },

		    tab(num,index) {
		    	let that = this;
		    	if((index+1) % 3 == 1) {
		    		that.triangleLeft = 8
		    		that.explainLeft = '0rpx'
		    	}else if((index+1) % 3 == 2) {
		    		that.explainLeft = '-248rpx'
		    		that.triangleLeft = 50
		    	}else {
		    		that.explainLeft = '-496rpx'
		    		that.triangleLeft = 86
		    	}
		        if(that._num == num) {
		            that.show = false;
		        	that._num = 0;
		            return false
		        }else {
			        let index;
			        that._num = num;
			        for (let i = 0; i < that.detail.rights.length; i++) {
			            if (num == that.detail.rights[i].id) {
			                index = i;
			            }
			        }
			        that.show = true;
			        that.move = false;
			        that.$forceUpdate();
		        }
		    },

		    change(e) {
		        let that = this;
		        let index = e.detail.current;
		        this.detail = this.list[index];
		        this.show = false;
		    },

            getList() {
                let that = this;
                that.$showLoading({
                    type: 'global',
                    text: '加载中...'
                });
                that.$request({
                    url: that.$api.member.all,
                    method: 'get',
                }).then(response=>{
                    that.$hideLoading();
                    if(response.code == 0) {
						that.list = response.data.list;
		                that.index = 0;
		                if (that.level > 0) {
		                    for (let i = 0; i < that.list.length; i++) {
		                        if (that.level == that.list[i].level) {
		                            that.index = i
		                        }
		                    }
		                }
		                that.detail = that.list[that.index];
		                that._num = that.list[that.index].rights[0].id;
		                that.content = that.list[that.index].rights[0].content;
		                if (that.list[that.index].rights.length == 2) {
		                    that.x = 33
		                } else if (that.list[that.index].rights.length == 1) {
		                    that.x = 50;
		                }
		                that.toView = 'to' + (that.detail.id); 
                    }else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(response => {
                    that.$hideLoading();
                });
            },
        },
        onLoad(options) { this.$commonLoad.onload(options);
        	let that = this;
	        if (options.level > 0) {
	            that.level = options.level;
	        } else {
	            that.level = 0;
	        }
	        if (options.prop > 0) {
	            that.prop = options.prop * 3;
	        } else {
	            that.prop = 0;
	        }
	        that.getList();
        }
    }
